import { unified } from "unified";
import rehypeDomStringify from "rehype-dom-stringify";

export default function parse(ast) {
  let story = [];
  // let currentUnit = {};
  ast.forEach((unit) => {
    const stringifiedChildren = unified()
      .use(rehypeDomStringify, { fragment: true })
      .stringify(unit);
    console.log(unit);
    console.log(stringifiedChildren);
    // if(unit.)
    switch (unit.tagName) {
      case "p":
        if (
          unit.children[0].tagName === "b" &&
          unit.children[0].children[0].trim().endswith(":")
        ) {
          story.push(";");
        }

        // if(story[story.length-1].type === "line" && story[story.length-1].speaker)
        break;
      case "blockquote":
        break;
    }

    // unit.type = "root";

    // console.debug(stringifiedChildren);
    // story.push({
    //     content: stringifiedChildren,
    // });
  });
  return story;
}
